import React, { useState, useEffect } from "react";
import {
  makeStyles,
  CssBaseline,
  Typography,
  Container,
  Link,
} from "@material-ui/core/";
import { COLOR_BRAND } from "../constants";
import {
  Icon28LogoVkOutline,
  Icon28LogoInstagram,
  Icon24LogoFacebook,
  Icon24LogoTwitter,
  Icon28MailOutline,
} from "@vkontakte/icons/dist/";
// import Icon28MailOutline from '@vkontakte/icons/dist/28/mail_outline';
// import Icon28LogoInstagram from '@vkontakte/icons/dist/28/logo_instagram';
// import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';
// import Icon24LogoFacebook from '@vkontakte/icons/dist/24/logo_facebook';
// import Icon24LogoTwitter from '@vkontakte/icons/dist/24/logo_twitter';

function Copyright() {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "start",
        alignItems: "center",
      }}
    >
      <Link color="inherit" href="https://vk.com/odsstudio" target="_blank">
        <Icon28LogoVkOutline height={32} width={32} fill={COLOR_BRAND} />
      </Link>

      <Link
        color="inherit"
        href="https://www.instagram.com/ods_studio_dev/"
        target="_blank"
      >
        <Icon28LogoInstagram height={32} width={32} fill={COLOR_BRAND} />
      </Link>
      {/* <Link
        color="inherit"
        href="mailto:ods.studio.info@gmail.com"
        target="_blank"
      >
        <Icon28MailOutline height={32} width={32} fill={COLOR_BRAND} />
      </Link> */}
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
    color: COLOR_BRAND,
    width: "100vw"

  },
  main: {
    marginTop: theme.spacing(8),
    marginBottom: theme.spacing(2),
  },
  footer: {
    color: COLOR_BRAND,
    padding: theme.spacing(3, 2),
    marginTop: "auto",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[200]
        : theme.palette.grey[800],
  },
}));

const Footer = ({}) => {
  const classes = useStyles();

  return (
    // <div className={classes.root}>
      <footer className={classes.footer}>
        <Container maxWidth="sm">
          <Typography variant="body1">Мы в соцсетях:</Typography>
          <Copyright />
        </Container>
        <Container maxWidth="sm">
          <Typography variant="body1">Напишите нам:</Typography>

          <Typography variant="body1">
            e-mail:{" "}
            <Link
              color="inherit"
              href="mailto:ods.studio.info@gmail.com"
              target="_blank"
              style={{ textDecoration: "underline " }}
            >
              ods.studio.info@gmail.com
            </Link>
          </Typography>
        </Container>
      </footer>
    // </div>
  );
};
export { Footer };
